window.addEventListener('load', () => {
    const orderForm = new OrderForm('order-form');
    orderForm.init();
});

class OrderForm {
  static apiURL = "http://localhost:8080"; // –ø—É—Ç—å –Ω–∞ —Å–µ—Ä–≤–µ—Ä–µ - –ø–µ—Ä–µ–Ω–µ—Å–µ–º –≤ env.

  // C–ø–∏—Å–æ–∫ –æ–±—Ä–∞–±–∞—Ç—ã–≤–∞–µ–º—ã—Ö –ø–æ–ª–µ–π
  static fields = [
    'face',
    'city',
    'initials',
    'telephone',
    'email',
    'originalLang',
    'targetLang',
    'image',
    'date',
    'comment',
    'privacy'
  ];

  constructor(orderFormId) {
    this.id = orderFormId;
    this.html = document.getElementById(orderFormId);
    this.data = {};
  }

  init() {
    this.html.addEventListener('submit', async (e) => {
      e.preventDefault();
      this.collectData();

      if (this.validateValues()) {
        await this.sendFormData();
      }
      this.printValues(); // –Ω–∞ –≤—Ä–µ–º—è —Ä–∞–∑—Ä–∞–±–æ—Ç–∫–∏
    });
  }

  collectData() {
    OrderForm.fields.forEach((field) => {
      this.data[field] = this.html[field].value;
    });
    this.files = this.html.file.files;
    console.log(this.files);
  }

  // –ù–∞ –≤—Ä–µ–º—è —Ä–∞–∑—Ä–∞–±–æ—Ç–∫–∏ - –ø–æ—Ç–æ–º —É–¥–∞–ª–∏–º
  printValues() {
    console.log(JSON.stringify(this.data));
  };

  validateValues() {
    // –≠—Ç–æ –ø—Ä–∏–º–µ—Ä:
    if (!this.data['city'] || this.data['city'].length <= 0) {
      this.showErrorMessage('–£–∫–∞–∂–∏—Ç–µ –≥–æ—Ä–æ–¥');
      return false;
    }
    // –ù–∞–ø–∏—à–µ–º, –∫–æ–≥–¥–∞ —Ä–µ—à–∏–º, —á—Ç–æ –∏ –∫–∞–∫ –≤–∞–ª–∏–¥–∏—Ä—É–µ–º
    return true;
  }

  async sendFormData() {
    // –ú–µ—Ç–æ–¥ –¥–ª—è –æ—Ç–ø—Ä–∞–≤–∫–∏ –¥–∞–Ω–Ω—ã—Ö –Ω–∞ —Å–µ—Ä–≤–µ—Ä - —Ñ–æ—Ä–º–∞—Ç JSON, –ø–æ–ª—è –ø–æ —Å–ø–∏—Å–∫—É
    try {
      const formData = new FormData();

      Object.keys(this.data).forEach((key) => {
        formData.set(key, this.data[key]);
      });
      Object.keys(this.files).forEach((key) => {
        formData.append(`files[]`, this.files[key], this.files[key].name);
      });

      const request = await fetch(OrderForm.apiURL, {
        method: 'POST',
        body: formData,
        });
      if (request.ok) {
        const data = await request.text();
        console.log('From server: ');
        console.log(data);
        console.log(request.status, request.statusText);
        // showSuccessMessage();
      }
    } catch (e) {
      this.showErrorMessage(e.message);
    }

  }

  showErrorMessage(message) {
    // –ú–µ—Ç–æ–¥ –¥–ª—è –ø–æ–∫–∞–∑–∞ —Å–æ–æ–±—â–µ–Ω–∏—è –æ–± –æ—à–∏–±–∫–µ –ø—Ä–∏ –≤–∞–ª–∏–¥–∞—Ü–∏–∏
    console.log('–û—à–∏–±–∫–∞: ', message);
  }

  showSuccessMessage() {
    // –ú–µ—Ç–æ–¥ –¥–ª—è –ø–æ–∫–∞–∑–∞ —Å–æ–æ–±—â–µ–Ω–∏—è –æ–± —É—Å–ø–µ—à–Ω–æ–π –æ—Ç–ø—Ä–∞–≤–∫–µ —Ñ–æ—Ä–º—ã –Ω–∞ —Å–µ—Ä–≤–µ—Ä
    console.log('–î–∞–Ω–Ω—ã–µ —É—Å–ø–µ—à–Ω–æ –æ—Ç–ø—Ä–∞–≤–ª–µ–Ω—ã');
  }
}
